@import 'app/styles/sass/mixins.scss';

.logo {
	background-repeat: no-repeat;
	background-position: center;
	background-size: contain;

	&.main {
		background-image: url('/images/logo.svg');
		width: 221px;
		height: 54px;
		cursor: pointer;
		flex-shrink: 0;
	}

	&.insurance {
		background-image: url('/images/logo.svg');
		width: 50px;
		height: 50px;
	}

	&.result {
		background-color: #d3d3d3;
		width: 64px;
		height: 64px;
		border-radius: 12px;
	}

	&.sberbank {
		background-image: url('/images/logo_sber.svg');
		width: 269px;
		height: 25px;
	}
}
